@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Ubuntu', 'sans-serif'
}

.app {
  width: 100%;
  height:auto;
  background-color: #1a2733;
}


::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #19232e;
}

::-webkit-scrollbar {
  width: 6px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  height: 30%;
  background-color: rgba(255, 255, 255, 0.3);
}
/* main container */
.nav-container{
    width: 100%;
    height: 6vh;
    max-height: 6vh;
    min-height: 6vh;
    
    display: -webkit-flex;
    
    display: flex;
    position: -webkit-sticky;
    position: sticky;
    top: 0%;
    z-index: 5;
    background: #161f29;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

/* header style*/
.nav-container > .section {
    height: 6vh;
}

/* logo and name container*/
.nav-container>.name-space {
    width: 25%;
    position: relative;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

/* Logo and name styles*/
.nav-container .name-space>.logo {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    color: white;
    position: absolute;
}

.nav-container .name-space .logo > span {
    font-size: 2rem;
}

/* Resizing logo*/
.nav-container .name-space .logo>img {
    width: 4rem;
    height: 4rem;
}

/* Navigattion bar conainer*/
.nav-container>.navbar-container {
    width: 55%;
    
}

/* navbar style */
.nav-container .navbar-container>.navbar {
    width: 100%;
    height: 6vh;
    list-style: none;
    padding: 0;

}

/* navbar link style */
.nav-container .navbar-container .navbar>.li{
    width: calc(100%/3);
    height: 6vh;
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    position: relative;
    
}

.navbar > .li::after {
    content: '';
    width: 0;
    height: 3px;
    background-color: #fff;
    position: absolute;
    bottom: 0;
    transition: 0.5s;
}

.navbar > .li:hover::after {
   width: 55%;
}


.nav-container .navbar .li div .link  {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    color: crimson;
    font-weight: 400;
    font-size:  1.4rem;
    text-decoration: none;
}

.nav-container .navbar li .active {
    /* background-color: blue;  */
    width: 100%;
    height: 100%;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    
}

.nav-container .navbar li .active::after {
    content: '';
    width: 50%;
    height: 3px;
    background-color: rgba(255,255,255, 0.2);
    position: absolute;
    bottom: 0;
    
}


.social-link-container {
    width: 20%;
    height: 6vh;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
}

.nav-container>.nav-button {
    width: 2rem;
    height: 2rem;
    margin-right: 10px;
    cursor: pointer;
    display: none;
}

.line1, .line2, .line3 {
    width: 100%;
    height: calc(60%/3);
    margin-top: 2px;
    border-radius: 5px;
    background: #fff;
    position: relative;
   
}


@media screen and (max-width: 920px) {
    /* logo and name container*/
    .nav-container>.name-space {
        width: 30%; 
    }

    .nav-container .name-space .logo > span {
        font-size: 1.5rem;
    }

    .nav-container {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center;
    }
    
    /* Navigattion bar conainer*/
    .nav-container>.navbar-container {
        width: 45%;
    }

    .social-link-container {
        width: 25%;
    }
}

@media screen and (max-width: 700px) {
    
    
    /* logo and name container*/
    .nav-container>.name-space {
        width: 25%;
        position: relative;
    
        display: -webkit-flex;
    
        display: flex;
        -webkit-align-items: center;
                align-items: center;
    }

    .nav-container>.navbar-container{
        width: 100%;
        height: 36vh;
        position: fixed;
        top: 6vh;
        right: 0;
        z-index: 6;
        display: block;
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
        transition: -webkit-transform 0.65s;
        transition: transform 0.65s;
        transition: transform 0.65s, -webkit-transform 0.65s;
    }

    .nav-container .navbar-container>.navbar {
        height: 36vh;
        background-color: rgba(17, 17, 17, 1);
        position: relative;
        display: block;
    }
    
    /* navbar link style */
    .nav-container .navbar-container .navbar>.li{
       
       margin: 0;
       padding: 0;
      
       width: 100%;
       height: calc(100%/3);
    }

    .nav-container .navbar .li div .link {
        color: white;
    }

    .nav-container .navbar .li div .link:hover {
        color: crimson;
        -webkit-transform: scale(1.1);
                transform: scale(1.1);
    }
    
    .navbar > .li:hover::after {
       width: 0%;
       bottom: 0%;
       background-color: none;
    }

    .nav-container .navbar li .active {
        background-color: rgba(255,255,255, 0.1);
    }
    
    .nav-container .navbar li .active::after {
        background-color: rgba(255,255,255, 0.1);
        bottom: 10%;
    }

    .social-link-container {
        width: 40%;
        margin-left: 5rem;
    }

    .nav-container>.nav-button {
        display:block;
    }

}

.nav-container>.display {
    -webkit-transform: translateX(0%);
            transform: translateX(0%)
}
.containerfluid {
    width: 100%;
    height: 100%;
    background-color: #1a2733;
    background: #1a2733;
   
    margin: 0 auto;
    padding: 0 1rem;

    display: -webkit-flex;

    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    
}

/* first row element styles */
.containerfluid>.line {
    width: 100%;
    height: 40rem; 
    margin: 6rem auto;
    position: relative;
   
}

.containerfluid .intro {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    background-color: rgba(255, 255, 255, 0.025);
    border-radius: 2rem;
}



.containerfluid .line>.profile-text{
    padding: 1rem;
    text-align: center;
    background-color: rgba(255, 255, 255, 0.025);
    border-radius: 2rem;

    width: 60%;
    height: 100%;

}

.containerfluid .line .profile-text>h2 {
    margin-top: 1rem;
    color: crimson;
}

.containerfluid .line .profile-text>h5 {
    margin-top: 1rem;
    line-height: 2rem;
    color: white;
}


.containerfluid .intro>.profile-image {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;

    border-radius: 2rem;
  
    height: 100%;
    width: 40%;
}

.containerfluid .line .profile-image>img {
    height: 50%;
    width: 50%;

}

/* second row element styles */
.containerfluid .description{
    display: -webkit-flex;
    display: flex;
    height: 20rem; 
    -webkit-justify-content: center; 
            justify-content: center;
    color: #fff;
    text-align: center;
    position: relative;
}

.containerfluid .description>.content-box {
    padding: 1rem;
    background-color: rgba(255, 255, 255, 0.02);
    
    width: 100%;
    border-radius: 2rem;
}

.containerfluid .description .content-box>h2{
    color: crimson;
    margin-top: 0.5rem;
}

.containerfluid .description .content-box>h5{
    margin-top: 1rem;
    line-height: 2rem;
}


/* third row element styles */
.containerfluid>.skill {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;

    color: white;
    position: relative;
}

/* skill  styles*/
.containerfluid .skill>.skill-text{
    height: 100%;
    -webkit-flex: 3 1;
            flex: 3 1;

    background-color: rgba(255, 255, 255, 0.025);
    border-radius: 2rem;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

}

.containerfluid .skill .skill-text>h2{
    margin-top: 1rem;
    color: crimson;
}

.containerfluid .skill .skill-text>h5{
    line-height: 2rem;
    margin: 1.5rem;
    text-align: center;
}

.containerfluid .skill>.skill-list{
    -webkit-flex: 2 1;
            flex: 2 1;
    width: 100%;
    height: 100%;

    background-color: rgba(255, 255, 255, 0.04);
    border-radius: 2rem;

    display: -webkit-flex;

    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;

    margin-left: 1rem;
}

.containerfluid .skill .skill-list > ul{
    list-style: circle;
    padding: 0;
    margin-top: 1.5rem;
    font-size: 1.1rem;
    
}

.containerfluid .skill .skill-list>h2{
    margin-top: 1rem;
    color: crimson;
}

.containerfluid .skill .skill-list ul>li{
    color: white;
}


@media screen and (max-width: 700px) {
    .containerfluid{
        padding-bottom: 4rem;
    }
    .containerfluid .intro {
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .containerfluid .intro .profile-image {
        margin-top: 1rem;
        width: 100%;
    }

    .containerfluid .intro .profile-text {
        margin-top: 0.5rem;
        width: 100%;
    }

    .containerfluid .skill{
        height: 55rem;
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .containerfluid .skill .skill-list {
        margin-top: 1rem;
        margin-left: -0.4rem;
    }

    .containerfluid .skill .skill-text {
        margin-top: 4rem;
    }

    .containerfluid .line>.profile-text{
        width: 100%;
    }

    .containerfluid .line {
        margin: 0 auto;
    }

    .containerfluid .description {
        height: 25rem;
        margin-top: 2rem;
    }
}


