.contact-container {
    width: 100%;
    height: 100%;
    background-color: #1a2733;
    background: #1a2733;
    color: white;
    position: absolute;
    bottom: 0;
}

.contact-container .contact-section {
    margin: 7rem 4rem;
    border-radius: 2rem;
    background-color: rgba(255, 255, 255, 0.05);
}

.contact-container .contact-section .row>.contact-form {
   padding: 2rem;
   display: block;
   margin: 0 auto;
}

.contact-container .contact-section .row .contact-form div div div>.form-control {
    margin-top: 1rem;
}


.contact-container .contact-section .row{
    padding: 0;
    margin: 0;
    
}
.contact-container .contact-section .row .address-line {
    background-color: rgba(255, 255, 255, 0.05);
    width: 100%;
    height: 100%;
    position: relative;
    bottom: 0;
    
    border-bottom-left-radius: 2rem;
    border-bottom-right-radius: 2rem;
    padding-top: 1rem;
